  <div class="device-status">
    <TextTitle title="" />
    <BorderBox1 :w="448" :h="242">
      <div class="content bsizing">
        <div class="left" id="demoCanvas" src="/svga/AI-Loading-2.svga">
          <img class="camera translate-c" :src="require('@/assets/camera.png')" />
        </div>
        <div class="right">
          <div id="pie-chart"></div>
          <div class="pie-text flex-c translate-c">
            <div class="center">
              <span class="online">
                {{ online }}
              </span>
              <em>&nbsp;/&nbsp;</em>
              <span class="total">
                {{ +online + (+offline) }}
              </span>
            </div>
            <div class="pie-text-bg translate-c"></div>
          </div>
        </div>
      </div>
    </BorderBox1>
  </div>
</template>

<script>
import { TextTitle, BorderBox1 } from '@/components'
import * as echarts from 'echarts/core';
import {
    PieChart
} from 'echarts/charts';
import {
    TitleComponent,
    TooltipComponent,
    GridComponent,
    LegendComponent
} from 'echarts/components';
import {
    CanvasRenderer
} from 'echarts/renderers';
import SVGA from 'svgaplayerweb'

export default {
  components: {
    TextTitle,
    BorderBox1
  },
  props: {
    chartData: {
      type: Array,
      default: () => []
    },
    offline: [Number, String],
    online: [Number, String]
  },
  data() {
    return {
      // chartData: [
      //   { name: '', value: 27 },
      //   { name: '', value: 25 }
      // ],
      color: ['#267DFF', '#1D3173'],
      pieChart: null
    }
  },
  mounted() {
    echarts.use(
      [LegendComponent, TitleComponent, TooltipComponent, GridComponent, PieChart, CanvasRenderer]
    )
    
    this.pieChart = echarts.init(document.getElementById('pie-chart'))

    console.log('device, mounted', this.chartData)

    new SVGA.autoload(document.querySelector('#demoCanvas'))
  },
  methods: {
    initChart(data) {
      let color = this.color

      let option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: 0,
          right: 0,
          textStyle: {
            color: '#A3BACC'
          },
          itemWidth: 8,
          itemHeight: 8,
        },
        grid: {
          top: 0,
          left: 0,
          right: 48,
          bottom: 0
        },
        color,
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['45%', '60%'],
            center: ['32%', '48%'],
            avoidLabelOverlap: false,
            label: {
                show: false,
                position: 'center'
            },
            emphasis: {
              label: {
                show: false,
              }
            },
            labelLine: {
              show: false
            },
            data
          }
        ]
      }

      this.pieChart.setOption(option)
    }
  }
}
</script>

